<template>
  <v-container grid-list-xs>
    <v-card>
      <v-card-title>
        Scheduled Exams
        <v-spacer></v-spacer>
        <!-- <v-btn v-if="!addLink || !addLink.hide" :class="addLink ? addLink.class || 'primary' : 'primary'" :to="addLink ? addLink.url : '/resource/add/'+page">
          <v-icon v-if="addLink && addLink.icon">{{ addLink.icon }}</v-icon>
          <v-icon v-else>mdi-database-plus</v-icon>
          {{ $t("App.hardcoded-texts.add") }} {{$t(`App.fhir-resources-texts.${label}`)}}
        </v-btn> -->
      </v-card-title>
      <v-card-title>
        <v-autocomplete
          outlined
          dense
          label="Cadre Category"
          :items="cadreCategories"
          v-model="search.cadreCategory"
          item-text="display"
          item-key="code"
          return-object
          clearable
          @change="buildTerms()"
          @click:clear="clearSearch('name')"
        />
        <v-spacer></v-spacer>
        <v-autocomplete
          outlined
          dense
          label="Status"
          :items="resultsStatus"
          v-model="search.resultsStatus"
          item-text="display"
          item-key="code"
          return-object
          clearable
          @change="buildTerms()"
          @click:clear="clearSearch('name')"
        />
      </v-card-title>
      <v-card-subtitle
        v-if="error_message"
        class="white--text error"
      >{{ error_message }}</v-card-subtitle>
      <v-card-text>
        <v-data-table
          style="cursor: pointer"
          :headers="headers"
          :items="results"
          item-key="id"
          :options.sync="options"
          :server-items-length="total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="loading"
          class="elevation-1"
          @click:row="clickIt"
        ></v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      loading: false,
      loadingExams: false,
      search: {
        cadreCategory: '',
        hpnumber: '',
        omang: '',
        passport: ''
      },
      debug: "",
      terms: {},
      headers: [{
        text: "Cadre Category",
        value: "cadres"
      }, {
        text: "Assessments",
        value: "assessments"
      }, {
        text: "Start Date",
        value: "startdate"
      }, {
        text: "End Date",
        value: "enddate"
      }, {
        text: "Pass Mark",
        value: "passmark"
      }, {
        text: "Auto Enroll",
        value: "autoenroll"
      }, {
        text: "Results Status",
        value: "status"
      }],
      results: [],
      options: { itemsPerPage: 10 },
      total: 0,
      prevPage: -1,
      link: [],
      error_message: null,
      update_again: { rerun: false, restart: false },
      cadreCategories: [],
      resultsStatus: []
    }
  },
  watch: {
    options: {
      handler() {
        this.getData();
      },
      deep: true
    }
  },
  mounted: function() {
    this.getData(true);
    this.getCadreCategory()
    this.getResultsStatus()
  },
  methods: {
    clearSearch: function(field) {
      this.search[field] = ''
      this.buildTerms()
    },
    clickIt: function(record) {
      this.$router.push({
        path: "/custom/nmcb-scheduled-exam?path=/components/exams&exam=" + record.id
      })
    },
    checkRerun() {
      if ( !this.loading && this.update_again.rerun ) {
        this.getData( this.update_again.restart )
        this.update_again = { rerun: false, restart: false }
      }
    },
    async buildTerms() {
      this.terms = {}
      if(this.search.cadreCategory) {
        this.terms["nmcbexamcadrecategory"] = this.search.cadreCategory.code
      }
      if(this.search.omang) {
        this.terms.identifier = this.search.omang
      }
      if(this.search.passport) {
        this.terms.identifier = this.search.passport
      }
      this.getData(true);
    },
    getData(restart) {
      //console.log("getting data",restart)
      if ( this.loading ) {
        this.update_again.rerun = true
        this.update_again.restart = this.update_again.restart || restart
        return
      }
      this.loading = true;
      this.error_message = null;
      let url = "";
      if (restart) this.options.page = 1;
      if (this.options.page > 1) {
        if (this.options.page === this.prevPage - 1) {
          url = this.link.find(link => link.relation === "previous").url;
        } else if (this.options.page === this.prevPage + 1) {
          url = this.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Basic?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_profile=http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-profile') === -1) {
          url = url + '&_profile=http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-profile'
        }
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.options.itemsPerPage || 10;
        let sort = "";
        for (let idx in this.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.options.sortBy[idx];
        }
        url =
          "/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-profile" +
          "&_count=" + count +
          "&_sort=-_lastUpdated&_total=accurate"
        this.debug = url;
      }
      let sTerms = Object.keys(this.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.terms[term] ) ) {
          if ( this.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.terms[term].join(',')
          }
        } else if ( this.terms[term] ) {
          url += "&" + term + "=" + this.terms[term];
        }
      }

      this.prevPage = this.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.results = [];
          if (data.total > 0) {
            this.link = data.link;
            for (let entry of data.entry) {
              let result = { id: entry.resource.id, cadres: [], assessments: [] };
              result.passmark = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-passmark'
              }).valueDecimal
              result.startdate = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/start-date'
              }).valueDate
              result.enddate = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/end-date'
              }).valueDate
              let auto = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/exam-auto-enroll'
              })?.valueBoolean
              if(auto) {
                result.autoenroll = "Yes"
              } else {
                result.autoenroll = "No"
              }
              let cadres = entry.resource.extension.filter((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category'
              })
              for(let cadre of cadres) {
                result.cadres.push(cadre.valueCoding.display)
              }
              result.cadres = result.cadres.join(", ")
              let assessments = entry.resource.extension.filter((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-assessment-type'
              })
              for(let assessment of assessments) {
                result.assessments.push(assessment.valueCoding.display)
              }
              result.assessments = result.assessments.join(", ")
              result.status = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-examresults-status'
              })?.valueCoding?.display
              this.results.push(result);
            }
          }
          this.total = data.total;
          this.loading = false;
          this.checkRerun()
        }).catch(err => {
          this.loading = false;
          this.error_message = "Unable to load results.";
          this.checkRerun()
          console.log(err);
        });
      }).catch(err => {
        this.loading = false;
        this.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    },
    getCadreCategory() {
      this.$fhirutils.expand('nmcb-cadre-category-valueset').then((response) => {
        this.cadreCategories = response
      })
    },
    getResultsStatus() {
      this.$fhirutils.expand('nmcb-examresults-status-valueset').then((response) => {
        this.resultsStatus = response
      })
    }
  }
}
</script>